@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap');
:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
}

body{
  font-family: 'Raleway';
  background-color: #f4f5f9;
}
.App
{
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
.subtopic{
  margin-top: 50px;
  color: #f4f5f9;
}
.social{
  background-color: rgb(209, 207, 207);
}
.condiv
{
  text-align: center;
  background-color: #f4f5f9;
  flex:var(--condiv-flex);
  top: 30px; 
}

.home
{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  
}
.profilepic
{
  border-radius: 50%;
  width: 200px;
  margin: 30px;
}
.typingeffect
{
  font-size: var(--typing-font);
  margin-bottom: 30px;
}


a
{
  text-decoration: none;
  color: #f4f5f9;
}

p{
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p{
  margin: 10px;
}
.social
{
  margin-top: 20px;
  display: flex;
  bottom: 60px;
}
.social i
{
  padding-right: 30px;
  font-size: 25px;
  color: #f4f5f9;
}
.social i:hover
{
  color: #ffffff;
}
.widecard
{
  border:0px solid #f4f5f9;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover
{
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(92, 26, 26, 0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.active
{
  font-weight: bolder;
}
.skills ul li
{
  list-style-type:none;
  margin:30px;
}

@media only screen and (max-width: 1200px) {
  .condiv{
    padding-top: 30px;
  }
}
